.Trends {
  min-height: 80vh;

  h1 {
    text-align: center;
    padding: -10px;
    margin-top: -20px;
    font-size: 30px;
    font-weight: 800;
    color: white;
    padding-bottom: 20px;
  }

  .first-trend {
    @media (max-width: 600px) {
      min-height: 60vh;
    }

    min-height: 80vh;

    .indiv {
      ul {
        list-style: none;
        text-decoration: none;
        padding: 40px;
      }

      li {
        color: white;
        font-size: 1vw;
        font-weight: 800;
        border-bottom: solid 1px lightgray;
        padding: 6px;

        @media (max-width: 600px) {
          font-size: 4vw;
        }
      }

      li span {
        color: red;
      }
    }
  }
}

.All-trends {
  height: 100vh;
  background-color: black;
  overflow-y: scroll;

  @media (max-width: 600px) {
    height: 80vh;
  }

  .in-alltrends {
    width: 100%;
    min-height: 120px;
    border: solid 1px white;
    padding: 30px;
    display: flex;
    flex-wrap: nowrap;

    .img {
      flex: 1 1 30%;
      height: 100%;
      position: relative;
      margin: 20px auto 0;
      width: 100%;

      @media (max-width: 600px) {
        flex: 1 1 60%;
      }
    }

    p {
      color: white;
      padding: 5px;
      padding-left: 20px;
    }

    .information {
      flex: 1 1 100%;
      height: 100%;
    }
  }
}